"use client"

import React, { useEffect, useRef } from "react"

interface Props {
  src: string
  className?: string
}

export default function LandingVideo({ src, className }: Props) {
  const ref = useRef<HTMLVideoElement | null>(null)

  useEffect(() => {
    const el = ref.current
    if (!el) return

    const setStart = () => {
      try {
        // Try to set playback start at 4 seconds
        if (el.duration > 4) {
          el.currentTime = 4
        } else {
          // if metadata not ready or duration < 4, still attempt when canplay
          // nothing else to do here
        }
      } catch (e) {
        // ignore exceptions (some browsers restrict setting currentTime before enough data)
        // we'll also listen for canplay below
      }
    }

    const onCanPlay = () => {
      try {
        if (ref.current && ref.current.duration > 4) ref.current.currentTime = 4
      } catch (e) {
        // ignore
      }
    }

    el.addEventListener('loadedmetadata', setStart)
    el.addEventListener('canplay', onCanPlay)

    return () => {
      el.removeEventListener('loadedmetadata', setStart)
      el.removeEventListener('canplay', onCanPlay)
    }
  }, [src])

  return (
    <video
      ref={ref}
      src={src}
      controls
      preload="metadata"
      className={className}
    />
  )
}
